:root {
  --background-clr: rgb(238, 238, 238);
  --border-clr: rgba(34, 36, 38, 0.15);
  --clr-prm: rgb(238, 238, 238);
  --clr-prm-focus: rgb(222, 222, 222);
  --clr-sec: rgba(230, 230, 230, .7);
  --clr-txt: rgba(0,0,0,0.87);
  --clr-txt-light: rgba(0,0,0,0.5);
  --clr-txt-inverse: #FFF;
  --modal-clr: rgba(0, 0, 0, 0.9);

  /* Regular colors */
  --mythic:   rgb(218, 35, 17);
  --rare:     rgb(167, 139, 55);
  --uncommon: rgb(159, 165, 175);
  --common:   rgb(26, 23, 24);
      
  /* Opaque colors */
  --mythic-o:   rgba(218, 35, 17, 0.8);
  --rare-o:     rgba(167, 139, 55, 0.8);
  --uncommon-o: rgba(159, 165, 175, 0.8);
  --common-o:   rgba(26, 23, 24, 0.9);

  --primary:  rgb(33, 133, 208);
  --primary-o:  rgba(33, 133, 208, 0.8);

  --tab-gradient: linear-gradient(rgb(200, 200, 200), rgba(210, 210, 210, 0.9), rgba(210, 210, 210, 0.1));
  
  scroll-behavior: smooth;
}

.darkMode {
  --background-clr: #212121;
  --border-clr: rgba(255, 255, 255, 0.40);
  --clr-prm: rgb(51, 51, 51);
  --clr-prm-focus: rgba(60, 60, 60, 0.98);
  --clr-sec: rgba(56, 56, 56, 0.6);
  --clr-txt: #DEDEDE;
  --clr-txt-light: rgba(222, 222, 222, 0.5);
  --clr-txt-inverse: #000;
  --modal-clr: rgba(0, 0, 0, 0.85);

  --common:   rgb(229, 232, 231);
  --common-o: rgba(229, 232, 231, 0.9);

  --tab-gradient: linear-gradient(
      rgba(65, 65, 65, 1.000), rgba(65, 65, 65, 0.975), rgba(65, 65, 65, 0.950),
      rgba(65, 65, 65, 0.925), rgba(65, 65, 65, 0.900), rgba(65, 65, 65, 0.850),
      rgba(65, 65, 65, 0.800), rgba(65, 65, 65, 0.750), rgba(65, 65, 65, 0.700),
      rgba(65, 65, 65, 0.650), rgba(65, 65, 65, 0.600), rgba(65, 65, 65, 0.550),
      rgba(65, 65, 65, 0.500), rgba(65, 65, 65, 0.450), rgba(65, 65, 65, 0.400),
      rgba(65, 65, 65, 0.300), rgba(65, 65, 65, 0.200), rgba(65, 65, 65, 0.100),
      rgba(65, 65, 65, 0.050)
  );
}

body {
  background-color: var(--background-clr);
  background-attachment: fixed;
  background-image: url(../images/watermark_light.svg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  font-weight: bold;
}

body.darkMode {
  background-image: url(../images/watermark_dark.svg);
}

label {
  color: var(--clr-txt);
}

#contentContainer {
  position: relative;
  min-height: 100vh;
}

#thisIsTheDivThatKeepsTheFooterAtTheBottomOfThePage {
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 2.5rem;
}

/* Scrollbar styling */
html {
  --scroll-area: rgb(207, 216, 220);
  --scroll-bar: rgba(34, 36, 38, 0.45);
}

body::-webkit-scrollbar {
  width: 17px;
}

body {
  /* Always show scroll bar */
  overflow-y: scroll;

  scrollbar-width: thin;
  scrollbar-color: var(--scroll-bar) var(--scroll-area);
}

body::-webkit-scrollbar-track {
  background: var(--scroll-area);
}

body::-webkit-scrollbar-thumb {
  background-color: var(--scroll-bar) ;
  border-radius: 8px;
  border: 2px solid var(--scroll-area);
}

.ui.card, .cardSetList > .card {
  box-shadow: 0 1px 3px 0 var(--border-clr), 0 0 0 2px var(--border-clr);
}

.ui.center.aligned.header {
  color: var(--clr-txt);
  margin-bottom: 0;
}

.closeModalButton button {
  float: right;
  border-radius: 3px;
  width: 2.5rem;
  height: 2.5rem;
  font-size: 2em;
  border: 1px solid var(--clr-txt);
}

.closeModalButton button:hover {
  cursor: pointer;
  filter: brightness(0.8);
}

.closeModalButton i {
  margin: 0;
  width: auto;
  height: auto;
}
