.fixedModal {
    /* Keep model fixed, but allow background scroll */
    position: fixed !important;

    /* Make Modal text selectable */
    user-select: auto !important;
    -webkit-user-select: auto !important;
    -moz-user-select: auto !important;
}

.spacedModalHeader {
    display: flex;
    justify-content: space-between;
}

.invisible {
    visibility: hidden;
}

.closeButtonSpacer {
    width: 35px;
}
